* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.login {
  position: relative;
  height: 100vh;
  background: url("https://assets.nflxext.com/ffe/siteui/vlv3/d049a3bd-40ee-411b-9f16-d1def798d43b/d6aeb5a9-b14c-42d4-999e-c2f0d6c60f04/IN-en-20230313-popsignuptwoweeks-perspective_alpha_website_large.jpg")
    center no-repeat;
  background-size: cover;
}


.login_logo {
  width: 100px;
  position: fixed;
  top: 10px;
  left: 10px;
}

.login__button {
  background-color: #fc0a0a;
  padding: 10px 20px;
  color: #fff;
  border: 0;
  text-transform: uppercase;
  border-radius: 5px;
  position: fixed;
  top: 10px;
  right: 10px;
}
.login__body {
  color: #fff;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  left: 0px;
  right: 0px;
}
.login__body h1 {
  font-size: 30px;
  margin-bottom: 20px;
}
.login__body h2 {
  font-size: 22px;
  margin-bottom: 10px;
  font-weight: 400;
}
.login__body h3 {
  font-size: 16px;
  margin-bottom: 30px;
  font-weight: 400;
}
.login__body .login_form {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login__body .login_form input {
  width: 300px;
  height: 40px;
  padding: 0px 15px;
  border-radius: 5px;
}
.login__body .login_form button {
  height: 40px;
  border: 0px;
  padding: 0px 10px;
  background-color: #fc0a0a;
  font-size: 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  border-radius: 5px;
  color: white;
}
.linear_gradient {
  width: 100%;
  z-index: 1;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0) 100%
  );
}

.signupscreen {
  max-width: 350px;
  padding: 70px;
  background-color: rgba(0, 0, 0, 0.85);
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
}
.signupscreen form {
  display: grid;
  flex-direction: column;
}
.signupscreen form input {
  outline: 0;
  height: 40px;
  border: 0px;
  margin-bottom: 15px;
  border-radius: 5px;
  padding: 5px 15px;
}
.signupscreen form button {
  padding: 16px 20px;
  font-size: 16px;
  color: #fff;
  border: 0;
  border-radius: 5px;
  background-color: #e50914;
  cursor: pointer;
  margin-bottom: 20px;
}
.signupscreen form span {
  color: #fff;
  cursor: pointer;
}
.signupscreen form h4 {
  color: gray;
}
.signupscreen form h4:hover span {
  text-decoration: underline;
}
@media (max-width:668px){
  .login_form{
    display:flex;
    flex-direction: column;
    row-gap: 1rem;
  }
  .login_form button{
    
    font-size:15px;
  }
  .login_form input{
    font-size: 20px;
  }
  .signupscreen{
    padding:30px
  }
}