.Banner {
  height: 500px;
  position: relative;
  object-fit: contain;
  color: #fff;
}
.Banner_content {
  margin-left: 30px;
  padding-top: 140px;
  height: 200px;
}
.banner_description {
  width: 400px;
  line-height: 1.3;
  padding-top: 20px;
  font-size: 16px;
}
.banner_btn button {
  cursor: pointer;
  color: #fff;
  outline: 0;
  border: 0px;
  font-weight: 700;
  border-radius: 5px;
  padding: 10px 30px;
  background-color: red;
  margin-right: 10px;
  margin-top: 10px;
}
.banner_btn button:hover {
  background-color: white;
  color: black;
}
.banner_fade {
  height: 6rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(27, 27, 27, 0.61),
    #111
  );
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
@media (max-width: 600px) {
  .Banner {
    width: 100%;
    height:400px;
  }
  .Banner_content{
    margin-left: 15px;
   padding-top:100px;
   height:150px;
  }
  .Banner_title{
    font-size:1.6rem;
  }
  .banner_btn button{
    font-size:10px;
    padding:10px 10px;
  }
  .banner_description {
    width:15rem;
  }
}
