.row {
  color: white;
  margin-left: 20px;
}
.row_posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;


}
.row_posters::-webkit-scrollbar{
    display:none;
}
.rowposter {
  max-height: 100px;
  object-fit: contain;
  margin-left: 10px;
  width: 100%;
  cursor: pointer;
  transition: transform 450ms;
  border-radius: 1vw;
}
.rowposter:hover {
  opacity: 1;
  transform: scale(1.08);
}
.largeimg {
  max-height: 200px;
}

@media (max-width:687px){
  .row{
    margin-left:6px;
  }
  h2{
    font-size:1.2rem
  }
  .row_posters{
    padding-bottom: 10px;
    padding-left:0px;
  }
  .rowposter{
   width:120px;
  }
  .largeimg{
    max-height: 150px;
  }
}