* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
.Nav_content {
  display: flex;
  justify-content: space-between;
}
.Nav {
  position: fixed;
  top: 0px;
  width: 100%;
  height: 30px;
  padding: 10px;
  z-index: 1;
  transition: all 0.5s ease-in 0s;
}
.nav_log {
  width: 80px;
  left: 20px;
  cursor: pointer;
  top: 10px;
}
.singout_btn {
  padding: 8px 12px;
  color: white;
  background-color: #fc0a0a;
  border: 0px;
  border-radius: 10px;
  right: 20px;
  cursor: pointer;
}
.nav_black {
  background-color: black;
  height: 50px;
}
@media (max-width: 680px) {
  .Nav_content {
    display: flex;
    justify-content: space-between;
    max-width: 300px;

   
  }
  /* .Nav{
    position: fixed;
  } */
  .nav_log{
    width:60px;
    object-fit: contain;
  }

.nav_black {
  background-color: black;
  height: 40px;
}
.singout_btn{
  font-size:10px;
  padding:5px 12px
}
}